* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-size: 62.5%;
    font-family: sans-serif;
}

body {
    background-color: #999;
}

.App {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.button {
    margin: 1rem;
    padding: 2rem;
    border-radius: 10px;
    font-weight: bold;
    font-size: 1.6rem;
}

.click {
    background-color: aquamarine;
}

.click:hover {
    background-color: aqua;
}

.restart {
    background-color: #ddd;
}

.restart:hover {
    background-color: #eee;
}
